body {
  margin: 0;
  padding: 0;
  background-color: #fdfcf7;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Teko";
  color: #282827;
}

button {
  font-family: "Teko";
}

.text-xl {
  font-size: 5rem;
}

h1 {
  font-size: 5rem;
  margin: 0;
  padding: 0;
  line-height: 90%;
}

h2 {
  font-size: 2rem;
  margin: 0;
  padding: 0;
}

h3 {
  font-size: 1.17rem;
}

h4 {
  font-size: 13px;
}

.padding {
  padding: 0 2rem;
}

.font-rose {
  color: hsl(6.17deg 92.7% 77.67%);
}

.main-btn {
  margin-top: 1rem;
  display: inline-block;
  padding: 1rem 1.5rem; // Adjust padding for size
  font-size: 1.5rem; // Adjust font size to match your design
  font-weight: bold; // Keep it bold
  color: #b22222; // Dark red text color
  text-transform: uppercase; // Matches the capitalized text style
  background-color: transparent; // Transparent background
  border: 2px solid #b22222; // Border matches the text color
  cursor: pointer;
  transition: all 0.3s ease; // Smooth hover effect
  box-shadow: none;

  &:hover {
    background-color: #b22222; // Dark red background on hover
    color: #fff; // White text on hover
  }
}
