.container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 10vh;
  //   padding: 1rem;

  &__left,
  &__right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: left;
    flex: 1; // Makes both columns take equal space
    // max-width: 50%;

    img {
      width: 100%;
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
  }
}
